
.cake-container {
  text-align: center;
  height: 100%;
}
#cake {
  display: block;
  position: relative;
  left: 50%;
  margin-top: -10rem;
  margin-bottom: 0;
  margin-left: -100px;
}
#isLike {
  -webkit-transition: all 1s;
  transition: all 1s;
}
/* ============================================== Candle
*/
.bg {
  width: 100%;
  height: 100%;
  background: #ee9ca7;
  background: -webkit-linear-gradient(top left, #ee9ca7 0%, #ee9ca7 100%);
  background: linear-gradient(to bottom right, #ee9ca7 0%, #ee9ca7 100%);
}
.velas {
  background: #ffffff;
  border-radius: 10px;
  position: relative;
  top: 247px;
  margin-left: 49.5%;
  width: 5px;
  height: 35px;
  -webkit-transform: translateY(-300px);
  transform: translateY(-300px);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-animation: in 500ms 6s ease-out forwards;
  animation: in 500ms 6s ease-out forwards;
}
.velas:after,
.velas:before {
  background: rgba(255, 0, 0, 0.4);
  content: "";
  position: absolute;
  width: 100%;
  height: 2.22222222px;
}
.velas:after {
  top: 25%;
  left: 0;
}
.velas:before {
  top: 45%;
  left: 0;
}
/* ============================================== Fire
*/
.fuego {
  border-radius: 100%;
  position: absolute;
  top: -20px;
  left: 70%;
  margin-left: -2.2px;
  width: 6.66666667px;
  height: 18px;
}
.fuego:nth-child(1) {
  -webkit-animation: fuego 2s 6.5s infinite;
  animation: fuego 2s 6.5s infinite;
}
.fuego:nth-child(2) {
  -webkit-animation: fuego 1.5s 6.5s infinite;
  animation: fuego 1.5s 6.5s infinite;
}
.fuego:nth-child(3) {
  -webkit-animation: fuego 1s 6.5s infinite;
  animation: fuego 1s 6.5s infinite;
}
.fuego:nth-child(4) {
  -webkit-animation: fuego 0.5s 6.5s infinite;
  animation: fuego 0.5s 6.5s infinite;
}
.fuego:nth-child(5) {
  -webkit-animation: fuego 0.2s 6.5s infinite;
  animation: fuego 0.2s 6.5s infinite;
}
/* ============================================== Animation Fire
*/
@-webkit-keyframes fuego {
0%,
  100% {
    background: rgba(254, 248, 97, 0.5);
    box-shadow: 0 0 40px 10px rgba(248, 233, 209, 0.2);
    -webkit-transform: translateY(0) scale(1);
    transform: translateY(0) scale(1);
}
50% {
    background: rgba(255, 50, 0, 0.1);
    box-shadow: 0 0 40px 20px rgba(248, 233, 209, 0.2);
    -webkit-transform: translateY(-20px) scale(0);
    transform: translateY(-20px) scale(0);
}
}
@keyframes fuego {
0%,
  100% {
    background: rgba(254, 248, 97, 0.5);
    box-shadow: 0 0 40px 10px rgba(248, 233, 209, 0.2);
    -webkit-transform: translateY(0) scale(1);
    transform: translateY(0) scale(1);
}
50% {
    background: rgba(255, 50, 0, 0.1);
    box-shadow: 0 0 40px 20px rgba(248, 233, 209, 0.2);
    -webkit-transform: translateY(-20px) scale(0);
    transform: translateY(-20px) scale(0);
}
}
@-webkit-keyframes in {
to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
}
@keyframes in {
to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
}
.come {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
  -webkit-animation: coming 2s;
          animation: coming 2s;
  opacity: 1;
}
@keyframes coming {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes coming {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.bg-bubbles {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.bg-bubbles li {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  bottom: -160px;
  -webkit-animation: square 30s infinite;
  animation: square 30s infinite;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
}
.bg-bubbles li:nth-child(1) {
  left: 10%;
}
.bg-bubbles li:nth-child(2) {
  left: 20%;
  width: 80px;
  height: 80px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 17s;
  animation-duration: 17s;
}
.bg-bubbles li:nth-child(3) {
  left: 25%;
  -webkit-animation-delay: 4s;
  animation-delay: 4s;
}
.bg-bubbles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  -webkit-animation-duration: 22s;
  animation-duration: 22s;
  background-color: rgba(255, 255, 255, 0.25);
}
.bg-bubbles li:nth-child(5) {
  left: 70%;
}
.bg-bubbles li:nth-child(6) {
  left: 80%;
  width: 120px;
  height: 120px;
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
  background-color: rgba(255, 255, 255, 0.2);
}
.bg-bubbles li:nth-child(7) {
  left: 32%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 7s;
  animation-delay: 7s;
}
.bg-bubbles li:nth-child(8) {
  left: 55%;
  width: 20px;
  height: 20px;
  -webkit-animation-delay: 15s;
  animation-delay: 15s;
  -webkit-animation-duration: 40s;
  animation-duration: 40s;
}
.bg-bubbles li:nth-child(9) {
  left: 25%;
  width: 10px;
  height: 10px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 40s;
  animation-duration: 40s;
  background-color: rgba(255, 255, 255, 0.3);
}
.bg-bubbles li:nth-child(10) {
  left: 90%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 11s;
  animation-delay: 11s;
}
@-webkit-keyframes square {
0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
100% {
    -webkit-transform: translateY(-900px) rotate(600deg);
    transform: translateY(-900px) rotate(600deg);
}
}
@keyframes square {
0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
100% {
    -webkit-transform: translateY(-900px) rotate(600deg);
    transform: translateY(-900px) rotate(600deg);
}
}
.happy {
  position: absolute;
  opacity: 0;
  width: 230px;
  height: 137px;
  overflow: hidden;
  left: 50%;
  margin-left: -115px;
  top: 30%;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@keyframes happybirthdays {
0% {
    position: absolute;
    top: 45%;
    opacity: 0;
}
70% {
    position: absolute;
    top: 45%;
    opacity: 0;
}
100% {
    position: absolute;
    top: 49%;
    opacity: 1;
}
}
@-webkit-keyframes happybirthdays {
0% {
    position: absolute;
    top: 45%;
    opacity: 0;
}
70% {
    position: absolute;
    top: 45%;
    opacity: 0;
}
100% {
    position: absolute;
    top: 49%;
    opacity: 1;
}
}
.button-style1,
.button-style2 {
  position: absolute;
  top: 40%;
  opacity: 1;
  z-index: 100;
}
.button-style1 {
  position: absolute;
  top: 60%;
  opacity: 0;
}
.button-style2 {
  position: absolute;
  top: 60%;
  margin-left: -87px;
  left: 50%;
  opacity: 0;
}
.name {
  font-weight: bold;
  position: absolute;
  text-align: center;
  overflow: hidden;
  width: 100%;
  top: -100px;
  opacity: 0;
  font-size: 32px;
  color: #fff;
}
@keyframes btn1 {
0% {
    opacity: 0;
    top: 60%;
}
70% {
    opacity: 0;
    top: 60%;
}
100% {
    opacity: 1;
    top: 70%;
}
}
@-webkit-keyframes btn1 {
0% {
    opacity: 0;
    top: 60%;
}
70% {
    opacity: 0;
    top: 60%;
}
100% {
    opacity: 1;
    top: 70%;
}
}
@keyframes btn2 {
0% {
    opacity: 0;
    top: 60%;
}
70% {
    opacity: 0;
    top: 70%;
}
100% {
    opacity: 1;
    top: 85%;
}
}
@-webkit-keyframes btn2 {
0% {
    opacity: 0;
    top: 60%;
}
70% {
    opacity: 0;
    top: 70%;
}
100% {
    opacity: 1;
    top: 85%;
}
}
.lk1,
.lk2 {
  text-decoration: none;
  font-family: Microsoft YaHei;
  color: #fff;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.no-like {
  cursor: no-drop;
}
.link1,
.link2 {
  width: 9rem;
  height: 3rem;
  border: 2px solid #fff;
  -webkit-border: 1px solid #fff;
  border-radius: 1.5rem;
  font-size: 23px;
  text-align: center;
  background: #ee9ca7;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
}
.link2 {
  cursor: help;
}
.bt1,
.bt2 {
  position: relative;
  top: 0.6rem;
  vertical-align: middle;
}
a.lk1:hover {
  color: #ee9ca7;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.link1:hover {
  background-color: #fff;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid #BBB;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
}
a.lk2:hover {
  color: #ee9ca7;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.link2:hover {
  background-color: #fff;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid #BBB;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
}



